import { FC } from "react";
import { Button, HStack, IconButton, Tooltip, VStack, Text, useColorModeValue } from "@chakra-ui/react";

import { configs } from "shared/content/Content";
import { onResumeOpen, open } from "utils/Functions";
import { FacebookIcon, GitHubIcon, InstagramIcon, LinkedInIcon, MailIcon, YoutubeIcon, ScholarIcon, CVIcon } from "utils/Icons";

const LinksToIconMapper: Record<string, JSX.Element> = {
    linkedin: <LinkedInIcon />,
    github: <GitHubIcon />,
    facebook: <FacebookIcon />,
    instagram: <InstagramIcon />,
    youtube: <YoutubeIcon />,
    mail: <MailIcon />,
    google_scholar: <ScholarIcon />,
    curriculum_vitae:<CVIcon/>
};

interface Props {
    resume?: boolean;
    exclude?: Array<string>;
    delay?: number;
}

export const Socials: FC<Props> = ({ resume = true, exclude, delay = 800 }) => {
    const textColor = useColorModeValue("black.600", "black.300"); 
    return (
        <VStack  align="start">
            {resume && (
                <Button data-aos="fade" data-aos-delay={delay} size="lg" borderRadius="xl" onClick={onResumeOpen}>
                    Resume
                </Button>
            )}

            {configs.common.socials.map(
                (social) =>
                    !exclude?.includes(social.type) && (
                        // <Tooltip key={social.type} label={social.type} textTransform="capitalize">
                            <HStack
                                // spacing={3}
                                as="button"
                                onClick={() => open(social.link)}
                                aria-label={`${social.type}-button`}
                            >
                                <IconButton
                                    boxSize="15px" 
                
                                    p="0"
                                    variant="icon"
                                    aria-label={`${social.type}-icon`}
                                    icon={LinksToIconMapper[social.type]}
                                    fontSize={social.type === "mail" ? "18pt" : "16pt"}
                                />
                                <Text fontSize="14" fontWeight="700" color={textColor} textTransform="capitalize">
                                    {social.type === "curriculum_vitae"? "Curriculum Vitae (PDF)"    : social.type.replace("_", " ")}
    </Text>
                            </HStack>
                        // </Tooltip>
                    )
            )}
        </VStack>
    );
};
